* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
}

/* =========================Navbar========================== */

#demmynavbar {
  display: flex;
  background-color: rgb(0, 0, 0);
  justify-content: space-between;
  padding: 0px 50px 0px 70px;
  height: 70px;
}

.brandName {
  align-items: center;
  font-size: 1.5rem;
  text-decoration: none;
  font-weight: 600;
  margin: 10px 0px;
  color: #fff;
}
#brandText {
  color: #fff;
  text-decoration: none;
}

#list {
  list-style-type: none;
  display: flex;
  font-size: 1rem;
  align-items: center;
  margin: 10px 0px;
}

.list-item {
  margin: 0px 2px;
}

.link-tag {
  color: #ffffff;
  padding: 6px 10px;
  text-decoration: none;
  align-items: center;
  letter-spacing: 1px;
  font-weight: 500;
  border-radius: 20px;
  transition: all 0.3s ease;
}
.menu-btn {
  color: #fff;
}

.link-tag:hover {
  color: rgb(220, 20, 60);
  background-color: none;
}

.icon {
  display: none;
}
#click {
  display: none;
}
.icon-for-link {
  display: none;
}

/* =========================Header Text==================================== */

#header-text {
  color: #fdfdfd;
  font-family: Montserrat;
  font-weight: 600;
  text-decoration-line: underline;
  text-underline-offset: 3px;
}
#header-brand {
  display: inline-block;
  color: #dc143c;
  text-decoration-line: underline;
  text-underline-offset: 3px;
  font-weight: 600;
  font-family: Montserrat;
}
#header-remain-text {
  display: inline;
  margin: 0px 10px;
}

/* =========================News Tittle ================================== */
.card {
  padding: 5px 5px;
  margin: auto;
  background-color: rgba(240, 240, 240, 0.94);
  align-items: center;
  border-radius: 10px;
}
#card-img {
  width: 100%;
  padding: 0;
  margin: 0;
}
#card-tittle {
  font-family: Poppins;
  margin: 5px 0px;
}
#card-text {
  font-family: Montserrat;
  font-size: 1.2rem;
  margin: 8px 0px;
  font-weight: 500;
  padding: 15px 0px;
}
#card-button {
  margin: auto;
  width: 90%;
  border-radius: 10px;
}
#card-time {
  font-family: Montserrat;
  font-weight: 300;
  text-align: center;
  padding-top: 10px;
}

/* ===========================================footer ================================= */

.footer {
  display: flex;
  font-family: Poppins;
  flex-direction: column;
  width: 100%;
  height: 300px;
  align-items: center;
  background: #070000;
}

.footer-text {
  margin-top: 30px;
  display: inline-flex;
  letter-spacing: 1px;
  color: #ffffff;
  justify-content: space-around;
  margin-bottom: 20px;
}
.before-text {
  margin-top: 5px;
  color: rgba(255, 255, 255, 0.904);
  margin-right: 7px;
  font-weight: 400;
}

.footer-btn {
  color: white;
  border: none;
  border-radius: 20px;
  background-color: #dc143c;
  height: 40px;
  width: 150px;
  align-items: center;
  font-size: 15px;
  padding: 5px;
}
.footer-icon {
  color: #dc143c;
  font-size: 22px;
  letter-spacing: 5px;
  margin: 20px 0px;
  display: inline-flex;
}
.icon-fa{
  padding-right: 10px;
}
.icon-ins{
  padding-right:10px
}
.icon-git{
  padding-right:10px
}
.icon-pin{
  padding:0px;
}

.copyright-text {
  color: white;
  font-weight: 400;
  margin-top:10px;
}


/* ==================================================================Navbar Media Quree============================================================================== */

@media only screen and (max-width: 768px) {
  #demmynavbar {
    padding: 0px 50px 0px 25px;
  }
  .brandName {
    font-size: 1.3rem;
  }
  #header-brand {
    display: block;
    margin-left: 10px;
  }
  #header-remain-text {
    display: block;
    margin-left: 10px;
  }

  #list {
    position: fixed;
    top: 60px;
    left: -100%;
    background: #111;
    height: 55vh;
    width: 50%;
    padding-left: 17px;
    z-index: 1;
    display: block;
    transition: all 0.5s ease;
  }
  .list-item {
    padding: 10px 0px;
    margin: 0;
  }

  .link-tag {
    padding: 5px 0px;
  }

  .icon {
    display: block;
    color: #fff;
    align-items: center;
    margin: 10px 0px;
    cursor: pointer;
  }

  #click:checked ~ #list {
    left: 0%;
  }

  .icon-for-link {
    display: inline;

    padding: 0px 9px;
    color: rgb(220, 20, 60);
}
.footer-text{
  display: flex;
  flex-direction: column;
  text-align: center;
  
}



}
